"use client"

import { useEffect, useState, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { authService } from "@/lib/auth-service"
import { requestsService, type RequestData } from "@/lib/requests-service"
import { useRequestsWS, type WSMessage } from "@/hooks/use-requests-ws"
import Image from "next/image"
import { SiteBranding } from "@/components/navigation/site-branding"

export default function RequestsPage() {
  const router = useRouter()
  const [requests, setRequests] = useState<RequestData[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")
  const [userTeams, setUserTeams] = useState<string[]>([])

  const user = authService.getCurrentUser()

  // Fetch user's teams
  useEffect(() => {
    const token = authService.getAccessToken()
    if (!token) {
      router.push("/login")
      return
    }

    if (user?.team_ids) {
      setUserTeams(user.team_ids)
    }
  }, [user, router])

  // Fetch requests for user's teams
  const fetchRequests = useCallback(async () => {
    try {
      setLoading(true)
      setError("")

      if (!userTeams.length) {
        setRequests([])
        setLoading(false)
        return
      }

      const response = await requestsService.listRequests({
        ordering: "-created_at",
      })

      // Filter to only user's teams
      const filtered = response.results.filter((r: RequestData) => userTeams.includes(r.team_id))
      setRequests(filtered)
    } catch (err) {
      console.error("[v0] Error fetching requests:", err)
      setError(err instanceof Error ? err.message : "Failed to fetch requests")
    } finally {
      setLoading(false)
    }
  }, [userTeams])

  // WebSocket handler
  const handleWsEvent = useCallback(
    (message: WSMessage) => {
      if (userTeams.length === 0) return

      if (message.event === "request_created" || message.event === "request_updated") {
        if (userTeams.includes(message.request?.team_id)) {
          setRequests((prev) => {
            const filtered = prev.filter((r) => r.id !== message.request?.id)
            return [message.request, ...filtered].sort(
              (a, b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime(),
            )
          })
        }
      } else if (message.event === "state_transition") {
        setRequests((prev) => prev.map((r) => (r.id === message.request_id ? { ...r, status: message.to as any } : r)))
      }
    },
    [userTeams],
  )

  useRequestsWS(handleWsEvent)

  useEffect(() => {
    if (userTeams.length > 0) {
      fetchRequests()
    }
  }, [userTeams, fetchRequests])

  return (
    <div className="min-h-screen bg-white">
      {/* Header replaced; global layout already provides branding */}
      <div className="max-w-7xl mx-auto px-4 pt-6 flex items-center justify-between">
        <h1 className="text-2xl font-bold" style={{ color: "#002449" }}>
          My Requests
        </h1>
        <Link
          href="/requests/new"
          className="px-4 py-2 text-white font-medium rounded-lg transition-opacity"
          style={{ backgroundColor: "#f75700" }}
          onMouseEnter={(e) => (e.currentTarget.style.opacity = "0.9")}
          onMouseLeave={(e) => (e.currentTarget.style.opacity = "1")}
        >
          New Request
        </Link>
      </div>

      <main className="max-w-7xl mx-auto px-4 py-8">
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
            <p className="text-red-800">{error}</p>
          </div>
        )}

        {loading ? (
          <div className="text-center py-12">
            <p className="text-gray-600">Loading requests...</p>
          </div>
        ) : requests.length === 0 ? (
          <div className="text-center py-12">
            <p className="text-gray-600 mb-4">No requests yet</p>
            <Link
              href="/requests/new"
              className="inline-block px-4 py-2 text-white font-medium rounded-lg"
              style={{ backgroundColor: "#f75700" }}
            >
              Create Your First Request
            </Link>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {requests.map((request) => (
              <Link key={request.id} href={`/requests/${request.id}`}>
                <div className="bg-white border border-gray-200 rounded-lg p-6 hover:shadow-lg transition-shadow cursor-pointer h-full">
                  <div className="flex items-center justify-between mb-4">
                    <span
                      className="inline-block px-3 py-1 text-xs font-semibold text-white rounded-full"
                      style={{
                        backgroundColor:
                          request.category === "BOM"
                            ? "#002449"
                            : request.category === "ADDITIONAL"
                              ? "#007367"
                              : "#f75700",
                      }}
                    >
                      {request.category}
                    </span>
                    <span
                      className="inline-block px-3 py-1 text-xs font-semibold rounded-full"
                      style={{
                        backgroundColor:
                          request.status === "Submitted"
                            ? "#f0e0c1"
                            : request.status === "Processing"
                              ? "#e3f2fd"
                              : request.status === "Issued"
                                ? "#e8f5e9"
                                : "#ffebee",
                        color:
                          request.status === "Submitted"
                            ? "#f75700"
                            : request.status === "Processing"
                              ? "#002449"
                              : request.status === "Issued"
                                ? "#078e31"
                                : "#c62828",
                      }}
                    >
                      {request.status}
                    </span>
                  </div>

                  <h3 className="text-lg font-semibold text-gray-900 mb-2">{request.notes || "Untitled"}</h3>

                  {request.fabrication && (
                    <p className="text-sm text-gray-600 mb-2">
                      {request.fabrication.fab_type} - {request.fabrication.name}
                    </p>
                  )}

                  {request.bom_items?.length > 0 && (
                    <p className="text-sm text-gray-600 mb-2">{request.bom_items.length} items</p>
                  )}

                  <p className="text-xs text-gray-500">{new Date(request.created_at).toLocaleDateString()}</p>
                </div>
              </Link>
            ))}
          </div>
        )}
      </main>
    </div>
  )
}
